import { h } from 'preact';

const Card = ({ name, background, logo, description, keywords }) => (
  <div className="rounded shadow overflow-hidden transition hover:shadow-xl hover:scale">
    <div className="h-64 flex justify-center items-center" style={{ background }}>
      <img src={logo} alt="project-logo" className="mx-auto w-3/6" />
    </div>
    <div className="px-6 pt-4 pb-2">
      <div className="font-bold text-gray-900 sm:text-xl xl:text-base mb-2">{name}</div>
      <div className="text-gray-700 text-base mb-4">{description}</div>
      {keywords && keywords.length ? (
        <div className="flex flex-wrap justify-start -mx-2">
          {keywords.map(keyword => (
            <div className="inline-block bg-gray-200 rounded-full px-3 py-1 text-xs font-medium text-gray-800 m-1">
              {keyword}
            </div>
          ))}
        </div>
      ) : null}
    </div>
  </div>
);

export default Card;
