import { h, render } from 'preact';

let root;
function init() {
  let App = require('./components/app/app').default;
  root = render(<App />, document.querySelector('#app'), root);
}

// in development, set up HMR:
if (module.hot) {
  module.hot.accept('./components/app/app', () => requestAnimationFrame(init));
}

init();
